import { useState, useCallback, useEffect } from 'react';
import { useAuth } from './useAuth';
import { createBrowserSupabaseClient } from '@/lib/supabase/client';
import { UserProfiles } from '@/types/interfaces';

interface ProfileDetailsState {
  isLoading: boolean;
  error: string | null;
  detailedProfile: UserProfiles | null;
  lastFetched: number | null;
}

interface UserProfileDetailsHook {
  userProfileDetails: UserProfiles | null;
  loading: boolean;
  error: string | null;
  refetch: () => Promise<void>;
}

/**
 * ÌïÑÏöîÌïú ÏãúÏ†êÏóê ÌîÑÎ°úÌïÑ ÏÉÅÏÑ∏ Ï†ïÎ≥¥Î•º Ï°∞ÌöåÌïòÎäî ÌõÖ
 * Î°úÍ∑∏Ïù∏ ÏãúÏóêÎäî ÏÑ∏ÏÖò Í∏∞Î∞ò Í∏∞Î≥∏ ÌîÑÎ°úÌïÑÎßå ÏÇ¨Ïö©ÌïòÍ≥†,
 * ÎßàÏù¥ÌéòÏù¥ÏßÄ, Ìà¨Ìëú Îì±ÏóêÏÑú ÌïÑÏöîÌï† ÎïåÎßå DB Ï°∞Ìöå ÏàòÌñâ
 */
export function useProfileDetails(): UserProfileDetailsHook {
  const { user, userProfile } = useAuth();
  const [userProfileDetails, setUserProfileDetails] = useState<UserProfiles | null>(null);
  const [loading, setLoading] = useState<boolean>(false);
  const [error, setError] = useState<string | null>(null);

  const fetchUserProfileDetails = async () => {
    if (!user?.id) {
      setUserProfileDetails(null);
      setError(null);
      return;
    }

    setLoading(true);
    setError(null);

    try {
      const supabase = createBrowserSupabaseClient();
      const { data, error } = await supabase
        .from('user_profiles')
        .select('*')
        .eq('id', user.id)
        .single();

      if (error) {
        throw error;
      }

      setUserProfileDetails(data);
    } catch (err: any) {
      console.error('ÏÇ¨Ïö©Ïûê ÌîÑÎ°úÌïÑ ÏÉÅÏÑ∏ Ï†ïÎ≥¥ Ï°∞Ìöå Ïò§Î•ò:', err);
      setError(err.message || 'ÌîÑÎ°úÌïÑ Ï†ïÎ≥¥Î•º Î∂àÎü¨Ïò§Îäî Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.');
      setUserProfileDetails(null);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchUserProfileDetails();
  }, [user?.id]);

  /**
   * ÌîÑÎ°úÌïÑ ÏÉÅÏÑ∏ Ï†ïÎ≥¥ Ï°∞Ìöå (ÌïÑÏöîÏãúÏóêÎßå Ìò∏Ï∂ú)
   * @param force - Ï∫êÏãú Î¨¥ÏãúÌïòÍ≥† Í∞ïÏ†ú Ï°∞Ìöå Ïó¨Î∂Ä
   */
  const fetchProfileDetails = useCallback(async (force: boolean = false) => {
    if (!user?.id) {
      setError('Î°úÍ∑∏Ïù∏Ïù¥ ÌïÑÏöîÌï©ÎãàÎã§');
      return null;
    }

    // ÏµúÍ∑ºÏóê Ï°∞ÌöåÌñàÎã§Î©¥ Ïä§ÌÇµ (5Î∂Ñ Ï∫êÏãú)
    const now = Date.now();
    const cacheExpiry = 5 * 60 * 1000; // 5Î∂Ñ
    
    if (!force && userProfileDetails && userProfileDetails.created_at && (now - new Date(userProfileDetails.created_at).getTime()) < cacheExpiry) {
      console.log('üìã [useProfileDetails] Ï∫êÏãúÎêú ÏÉÅÏÑ∏ ÌîÑÎ°úÌïÑ ÏÇ¨Ïö©');
      return userProfileDetails;
    }

    setLoading(true);
    setError(null);
    
    const startTime = Date.now();
    
    try {
      console.log('üìã [useProfileDetails] ÌîÑÎ°úÌïÑ ÏÉÅÏÑ∏ Ï†ïÎ≥¥ Ï°∞Ìöå ÏãúÏûë');

      // Îã®ÏàúÌïú ÌÉÄÏûÑÏïÑÏõÉ ÏÑ§Ï†ï (5Ï¥à)
      const timeoutPromise = new Promise((_, reject) => {
        setTimeout(() => reject(new Error('ÌîÑÎ°úÌïÑ ÏÉÅÏÑ∏ Ï°∞Ìöå ÌÉÄÏûÑÏïÑÏõÉ')), 5000);
      });

      const supabase = createBrowserSupabaseClient();
      const profilePromise = supabase
        .from('user_profiles')
        .select(`
          id, email, nickname, avatar_url, is_admin, is_super_admin,
          star_candy, star_candy_bonus, birth_date, birth_time,
          gender, open_ages, open_gender, created_at, updated_at
        `)
        .eq('id', user.id)
        .single();

      const { data, error } = await Promise.race([
        profilePromise,
        timeoutPromise,
      ]) as any;

      const duration = Date.now() - startTime;
      
      if (error) {
        console.warn('‚ö†Ô∏è [useProfileDetails] ÌîÑÎ°úÌïÑ ÏÉÅÏÑ∏ Ï°∞Ìöå Ïã§Ìå®:', error);
        
        // DB Ï°∞Ìöå Ïã§Ìå® Ïãú ÏÑ∏ÏÖò Í∏∞Î∞ò ÌîÑÎ°úÌïÑ Ïú†ÏßÄ
        setUserProfileDetails(userProfile);
        
        return userProfile;
      }

      const detailedProfile = data as UserProfiles;
      
      console.log(`‚úÖ [useProfileDetails] ÌîÑÎ°úÌïÑ ÏÉÅÏÑ∏ Ï°∞Ìöå ÏÑ±Í≥µ (${duration}ms):`, {
        nickname: detailedProfile.nickname,
        starCandy: detailedProfile.star_candy,
        isAdmin: detailedProfile.is_admin,
      });

      // ÏÉÅÌÉú ÏóÖÎç∞Ïù¥Ìä∏
      setUserProfileDetails(detailedProfile);

      // ÏÉÅÏÑ∏ ÌîÑÎ°úÌïÑ Ï†ïÎ≥¥Î•º ÎÇ¥Î∂Ä ÏÉÅÌÉúÏóêÎßå Ï†ÄÏû• (AuthProviderÎäî ÏÑ∏ÏÖò Í∏∞Î∞ò ÌîÑÎ°úÌïÑ Ïú†ÏßÄ)
      return detailedProfile;

    } catch (error) {
      const duration = Date.now() - startTime;
      console.error(`‚ùå [useProfileDetails] ÌîÑÎ°úÌïÑ ÏÉÅÏÑ∏ Ï°∞Ìöå ÏòàÏô∏ (${duration}ms):`, error);
      
      setUserProfileDetails(userProfile);
      
      return userProfile;
    }
  }, [user?.id, userProfileDetails, userProfile]);

  /**
   * ÌäπÏ†ï Ï†ïÎ≥¥Í∞Ä ÌïÑÏöîÌïúÏßÄ ÌôïÏù∏ÌïòÎäî Ïú†Ìã∏Î¶¨Ìã∞ Ìï®ÏàòÎì§
   */
  const needsDetailedProfile = useCallback((requiredFields: Array<keyof UserProfiles>) => {
    if (!userProfile) return true;
    
    // ÏÑ∏ÏÖò Í∏∞Î∞ò ÌîÑÎ°úÌïÑÏóêÏÑú Ï†úÍ≥µÎêòÏßÄ ÏïäÎäî Ï†ïÎ≥¥Îì§ Ï≤¥ÌÅ¨
    const detailOnlyFields: Array<keyof UserProfiles> = [
      'star_candy', 'star_candy_bonus', 'is_admin', 'is_super_admin',
      'birth_date', 'birth_time', 'gender', 'open_ages', 'open_gender'
    ];
    
    return requiredFields.some(field => detailOnlyFields.includes(field));
  }, [userProfile]);

  /**
   * ÎßàÏù¥ÌéòÏù¥ÏßÄÏö© ÌîÑÎ°úÌïÑ Ï°∞Ìöå
   */
  const fetchForMyPage = useCallback(async () => {
    console.log('üë§ [useProfileDetails] ÎßàÏù¥ÌéòÏù¥ÏßÄÏö© ÌîÑÎ°úÌïÑ Ï°∞Ìöå');
    return await fetchProfileDetails();
  }, [fetchProfileDetails]);

  /**
   * Ìà¨ÌëúÏö© ÌîÑÎ°úÌïÑ Ï°∞Ìöå (Î≥ÑÏÇ¨ÌÉï, Í∂åÌïú ÌôïÏù∏)
   */
  const fetchForVoting = useCallback(async () => {
    console.log('üó≥Ô∏è [useProfileDetails] Ìà¨ÌëúÏö© ÌîÑÎ°úÌïÑ Ï°∞Ìöå');
    return await fetchProfileDetails();
  }, [fetchProfileDetails]);

  /**
   * Í¥ÄÎ¶¨Ïûê Í∂åÌïú ÌôïÏù∏
   */
  const checkAdminStatus = useCallback(async () => {
    if (userProfile?.is_admin !== undefined) {
      return userProfile.is_admin;
    }
    
    const detailed = await fetchProfileDetails();
    return detailed?.is_admin || false;
  }, [userProfile?.is_admin, fetchProfileDetails]);

  return {
    userProfileDetails,
    loading,
    error,
    refetch: fetchUserProfileDetails,
  };
} 